/* App.css */

body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.app-container {
  display: flex;
  height: 100vh;
}

.sidebar {
  width: 200px;
  background-color: #282c34;
  color: white;
  padding: 1rem;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.sidebar ul {
  list-style: none;
  padding: 0;
}

.sidebar ul li {
  margin: 1rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.381);
}

.sidebar ul li a {
  text-decoration: none;
  color: #61dafb;
  font-weight: bold;
}

.sidebar ul li a:hover {
  color: white;
}

.content {
  flex: 1;
  padding: 2rem;
  background-color: #f5f5f5;
  overflow-y: auto;
}

h1 {
  color: #282c34;
}
